  async testImageCompression(file: File) {
    if (!file) {
      console.log('Aucun fichier slectionn.');
      return;
    }
  
    console.log('Taille originale:', file.size / 1024, 'Ko');
  
    // Options de compression
    const options = {
      maxSizeMB: 1,
      maxWidthOrHeight: 1000,
      useWebWorker: true,
    };
  
    try {
      const compressedFile = await imageCompression(file, options);
      console.log('Taille aprs compression:', compressedFile.size / 1024, 'Ko');
  
      // Vrifier si le fichier est bien en PNG/JPEG aprs compression
      if (compressedFile.type !== 'image/png' && compressedFile.type !== 'image/jpeg') {
        console.error('Le fichier compress n\'est pas un format support (PNG ou JPEG).');
        this.errorMessage = 'Erreur de compression : Le format de l\'image n\'est pas valide.';
        return;
      }
      
  
      // Lire l'image compresse et afficher l'aperu
      const reader = new FileReader();
      reader.onload = (e) => {
        this.newPhotoUrl = e.target?.result as string;
        console.log('Image compresse prte  tre affiche !');
      };
      reader.readAsDataURL(compressedFile);
    } catch (error) {
      console.error('Erreur lors de la compression:', error);
    }
  } 

