  onToggleChange(event: Event) {
    const checkbox = event.target as HTMLInputElement;
    const newValue = checkbox.checked;

    if (newValue && this.boutiqueIdSelected.length === 0) {
      this.errorMessage = "Veuillez slectionner au moins une boutique avant d'ajouter des stocks.";
      this.showBoutiqueError = true;
      // Annule l'activation du switch
      setTimeout(() => {
        this.isChecked = false;
      }, 0);
    } else {
      this.isChecked = newValue;
      // Efface le message si le switch est dsactiv
      if (this.errorMessage === "Veuillez slectionner au moins une boutique avant d'ajouter des stocks.") {
        this.errorMessage = '';
      }
    }
  }
  
  onFileSelected(event: Event): void {
    const input = event.target as HTMLInputElement;
    if (input.files && input.files.length > 0) {
      const file = input.files[0];
  
      // Vrification du format du fichier
      const allowedFormats = ['image/jpeg', 'image/png', 'image/jpg'];
      if (!allowedFormats.includes(file.type)) {
        this.errorMessage = 'Le fichier doit tre une image (JPG, PNG)';
        return;
      }
      this.selectedFile = file;
      this.imageFile = file;
  
      // Tester la compression
      this.testImageCompression(file);

      const reader = new FileReader();
      reader.onload = (e) => {
        this.newPhotoUrl = e.target?.result as string;
      };
      reader.readAsDataURL(this.selectedFile);
    }
  }
  
